.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  display: flex;
}

.card__img {
  height: 134px;
  margin: 32px 19px 0;
}

.card__description {
  color: #060b35;
  margin-top: 40px;
  margin-inline: 16px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  margin-inline: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars-rating {
  margin-inline: 16px;
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  text-align: right;
  justify-content: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__buy {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__buy:hover {
  color: #00acdc;
  background: #fff;
}

.card__price {
  justify-content: space-between;
  margin-inline: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  text-align: right;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__image {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__image:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

* {
  box-sizing: border-box;
}

